
import { Options, Vue } from 'vue-class-component';


@Options({
  props: {
    placeholder: {
      type: String,
      required: false,
      default: ''
    },
    type: {
      type: String,
      required: false,
      default: 'text'
    },
    disabled: {
      type: Boolean,
      required: false,
      default: false
    },
    modelValue: String
  },
  emits: ['update:modelValue']
})
export default class InputComponent extends Vue {

  public changeValue(value: string): void {
    this.$emit('update:modelValue', value);
  }
}
